.Layout {
    background: #fff;
    height: 100%;
}

.Header {
    padding: 0;
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14),
        0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    position: fixed;
    width: 100%;
    height: auto;
    box-sizing: border-box;
    background: #fff;
}

.TopHeader {
    background: linear-gradient(
        90deg,
        #ff2400,
        #e81d1d,
        #e8b71d,
        #e3e81d,
        #1de840,
        #1ddde8,
        #2b1de8,
        #dd00f3,
        #dd00f3
    );
    background: #455a64;
    background: #375e63;
    padding: 0;
    line-height: 50px;
    height: 50px;
    width: 100%;
    padding: 4px;
    padding-left: 8px;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
}

.BottomHeader > div {
    padding-top: 5px;
    padding-bottom: 5px;
}

.Main {
    background: #fafafa;
    margin-top: 100px;
    min-height: calc(100% - 100px);
    flex: none;
}

.Main > div {
    margin-bottom: 32px;
    margin-top: 8px;
    padding: 0 8px;
    box-sizing: border-box;
    margin: 24px auto;
    margin-bottom: 64px;
    max-width: 960px;
    width: 100%;
}

:global(.ant-card) {
    background-color: #fff;
    margin: 8px 0px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
        0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
    border-radius: 2px;
}

.Button {
    color: #fff !important;
}

:global(.ant-menu-vertical) {
    border-right: none;
}

.MenuButtonContiner {
    flex: 1 1 auto;
    overflow: hidden;
    display: flex;
    align-items: center;
}

.QuickButtonContiner {
    flex: 0 0 auto;
    justify-content: flex-end;
    display: flex;
    align-items: center;
}
